.deleteMessage {
  width: 100%;
  min-height: 100vh;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center; }
  .deleteMessage .messageContainer {
    margin: 1.5rem;
    padding: 2rem;
    background-color: white;
    border-radius: 1rem;
    border: black solid 0.3rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: black 10px 10px 0px; }
  .deleteMessage button {
    transition: 0.5s;
    width: 100px;
    margin: 1rem;
    padding: 0.7rem;
    border-radius: 1rem;
    border: black solid 0.3rem;
    box-shadow: black 5px 5px 0px; }
  .deleteMessage button:hover {
    transform: translateY(0.7rem);
    box-shadow: black 0px 0px 0px; }
  .deleteMessage .delete-button {
    color: white;
    background-color: crimson; }
